import type { IFullRegion } from '../interface';

/**
 * 获取 node 上的 align 对齐点 相对于页面的坐标
 * 比如说要得到target元素的对齐点位置,对齐在 target 的[br]
 * 首先传入的参数{left,top} 是 target的左上角位置
 * br 对应bottom-right即右下角，那么
 * right = left + width
 * bottom = top + height
 * 如果有 center , 则width/height要除于 2
 */
function getAlignOffset(region: IFullRegion, align: string): { left: number, top: number} {
  const V = align.charAt(0);
  const H = align.charAt(1);
  const w = region.width;
  const h = region.height;

  let x = region.left;
  let y = region.top;

  if (V === 'c') {
    y += h / 2;
  } else if (V === 'b') {
    y += h;
  }

  if (H === 'c') {
    x += w / 2;
  } else if (H === 'r') {
    x += w;
  }

  return {
    left: x,
    top: y,
  };
}

export default getAlignOffset;
